import styles from "./NewsComponent.module.css";
import {FC, RefObject, useRef} from "react";
import AliceCarousel from "react-alice-carousel";
import {NewComponent} from "@components/NewComponent/NewComponent";

import 'react-alice-carousel/lib/alice-carousel.css';



type Props = {
  scrollerRef: RefObject<HTMLElement>;
}

const items = [
  <NewComponent src={'https://cdn.b12.io/client_media/VhBHooYp/11b0cfb5-8438-11f0-8c1d-0242ac110002-jpg-hero_image.jpeg'} title={'    '} description={'  ,         ,    .       ,        .     .'} />,
  <NewComponent src={'https://cdn.b12.io/client_media/VhBHooYp/11b0cfb5-8438-11f0-8c1d-0242ac110002-jpg-hero_image.jpeg'} title={'   '} description={'      ,         .  ,     ,    ,      .  ...'} />,
  <NewComponent src={'https://cdn.b12.io/client_media/VhBHooYp/11b0cfb5-8438-11f0-8c1d-0242ac110002-jpg-hero_image.jpeg'} title={'   '} description={'      !  ,          ,        ,     .   ...'} />
]

export const NewsComponent: FC<Props> = ({scrollerRef, ...props}) => {


  const triggerRef = useRef<HTMLElement>(null);
  const contentRef = useRef<HTMLDivElement>(null);


  const responsive = {
    0: { items: 1 },     //    1 
    1024: { items: 2 },  //   1024px  2   
  };

  return (
    <section ref={triggerRef} className={styles.main}>
      <div ref={contentRef} className={styles.content}>
        <AliceCarousel mouseTracking items={items}
                       responsive={responsive}
                       infinite
                       controlsStrategy="responsive"/>
      </div>
    </section>
  )

}